/* Based on the work of Chris Rickels (https://codepen.io/cRckls/pen/DJvZXo), but quite modified by me */

.crt {
  overflow: hidden;
  position: relative;
}

.crt::after {
  content: " ";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(18, 16, 16, 0.1);
  opacity: 0;
  z-index: 999;
  pointer-events: none;
  /* Slow down and allow pausing */
  animation: flicker 1.2s infinite;
}

.crt::before {
    content: " ";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: linear-gradient(
        to bottom,
        rgba(255,255,255,0),
        rgba(255,255,255,0) 50%,
        rgba(0,0,0,0.2) 70%,
        rgba(0,0,0,0.6)
    );
    z-index: 5;
    background-size: 100% 3px, 4px 100%;
    pointer-events: none;
}

/* Pause CRT flicker while scrolling */
.is-scrolling .crt::after {
  animation-play-state: paused !important;
}

/* Respect reduced motion: disable flicker */
@media (prefers-reduced-motion: reduce) {
  .crt::after {
    animation: none !important;
    opacity: 0.12;
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes flicker {
  0% { opacity: 0.15; }
  50% { opacity: 0.3; }
  100% { opacity: 0.15; }
}

@keyframes blur {
  0%, 100% { opacity: 0.85; }
  50% { opacity: 1; }
}

@keyframes jerk {
  50% {
    transform: translateX(1px);
  }
  51% {
    transform: translateX(0);
  }
}

@keyframes jerkup {
  50% {
    transform: translateY(1px);
  }
  51% {
    transform: translateY(0);
  }
}

@keyframes jerkblue {
  0%,
  30% {
    transform: translateX(0);
  }
  31% {
    transform: translateX(5px);
  }
  32%,
  98% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(5px);
  }
}

@keyframes jerkgreen {
  0%,
  30% {
    transform: translateX(0);
  }
  31% {
    transform: translateX(-5px);
  }
  32%,
  98% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-5px);
  }
}

@keyframes jerkwhole {
